<h1 class="title">{{"開戰！"}}</h1><section class="filter"><section class="tags"><name-tag v-for="(obj, idx) in nameWithState" :key="`tag-${idx}`" :name="obj.name" :available="obj.available" :nameId="idx"></name-tag></section><button class="start" @click="setGroup">開始分組</button><section class="result" v-if="group.length &gt; 0"><section class="group"><p class="name name_1" v-for="(nameId, i) in group[0]" :key="`name-0-${i}`">{{names[nameId]}}</p></section><section class="group"><p class="name name_2" v-for="(nameId, i) in group[1]" :key="`name-1-${i}`">{{names[nameId]}}</p></section></section></section>