const state = {
  available: [],
  group: [],
  names: [
    '馬卡龍',
    '豆豆龍',
    '保麗龍',
    '下面一條龍',
    '馬水車龍',
    '巨龍',
    '好色龍',
    '泡泡龍'
  ],
}

const getters = {
  nameWithState: state => {
    return state.names.map((n, idx) => {
      return {
        name: n,
        available: state.available.includes(idx)
      }
    })
  },
  getNameById: (state, nameId) =>  {
    console.log('get name by id')
    console.log(nameId)
    console.log(state.group)
    return state.names[nameId]
  }
}

const mutations = {
  setAvailableById: (state, nameId) => {
    if (state.available.indexOf(nameId) !== -1) {
      state.available = state.available.filter(i => i !== nameId)
    }
    else {
      state.available.push(nameId);
    }
    console.log(state.available)
  },
  setGroup: state => {
    let arr =  state.available.map(x => x);                                                        
    let group = []

    for (let i = 0; i < Math.floor(state.available.length / 2); ++i) {
      const num = arr[Math.floor(Math.random() * arr.length)]
      group.push(num)
      arr = arr.filter(n => n !== num)
    }
    console.log(state.available)
    console.log([group, arr])

    state.group = [
      group,
      arr
    ]
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations
}